.home__container {
    row-gap: 3rem;
}

.home__content {
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
} 

.home__social-icon {
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover {
    color: var(--title-color-dark);
}

.home__title {
    font-size: var(--big-font-size);
    /* margin-bottom: var(--mb-0-15); */
}

.home__hand {
    width: 38px;
    height: 38px;
    margin-left: 0.4rem;
}

.home__subtitle {
    position: relative;
    font-size: var(--h1-font-size);
    padding-right: 2.4rem;
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-0-15);
}

.home__subtitle1 {
    margin-bottom: var(--mb-0-5);
}

.home__img {
    background: url(../../Assets/profile.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgba(11, 11, 11, 0.3);
    order: 1;
    justify-self: center;
    width: 300px;
    height: 300px;
    animation: profile__animate 8s ease-in-out infinite 1s;
}

@keyframes profile__animate {
    0% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }

    50% {
        border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
    }

    100% {
        border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
    }
}

.home__scroll {
    margin-left: 9.25rem;
}

.wheel {
    animation: scroll 2s ease infinite;
}

@keyframes scroll {
    0% {
        transform: translateY(0);
    } 
    
    30% {
        transform: translateY(3.75rem);
    } 
}

.home__scroll-name {
    color: var(--title-color);
    font-weight: var(--font-medium);
    margin-right: var(--mb-0-25);
    margin-left: 2px;
}

.home__scroll-arrow {
    font-size: 1.25rem;
    color: var(--title-color);
}

/* For Large Device */
@media screen and (max-width: 992px){
    .home__content {
        grid-template-columns: 100px repeat(2, 1fr);
        column-gap: 1.25rem;
    }

    .home__title {
        font-size: var(--h1-font-size);
    }

    .home__hand {
        width: 26px;
        height: 26px;
    }

    .home__subtitle {
        /* padding-left: 3.75rem; */
        font-size: var(--h2-font-size);
        margin-bottom: var(--mb-0-25);
    }

    .home__subtitle:before {
        width: 42px;
        top: 0.8px;
    }

    .home__img {
        width: 250px;
        height: 250px;
        box-shadow: inset 0 0 0 8px rgba(11, 11, 11, 0.3);
    }

    .home__scroll {
        margin-left: 7.5rem;
    }
  }
  
  /* For Medium Device */
  @media screen and (max-width: 768px){
    .home__content {
        grid-template-columns: 1.5fr 3fr;
        padding-top: 0;
    }

    .home__img {
        order: initial;
        justify-self: initial;
        width: 200px;
        height: 200px;
        box-shadow: inset 0 0 0 6px rgba(11, 11, 11, 0.3);
    }

    .home__title {
        font-size: var(--h1-font-size);
        margin-bottom: var(--mb-0-1);
    }

    .home__hand {
        width: 22px;
        height: 22px;
    }

    .home__subtitle {
        font-size: var(--h2-font-size);
        margin-bottom: var(--mb-0-1);
        
    }

    .home__subtitle1 {
        font-size: var(--h4-font-size);
        margin-bottom: var(--mb-0-5);
        
    }

    /* .home__description {
        max-width: initial;
        margin-bottom: var(--mb-1);
    } */

    .home__data {
        grid-column: 2/4;
    }

    .home__scroll {
        display: none;
    }


  }

  
  /* For Small Device */
  @media screen and (max-width: 350px){
    .home__img {
        width: 180px;
        height: 180px;
    }

    .home__hand {
        width: 22px;
        height: 22px;
    }

    .home__data {
        grid-column: 2/4;
    }
    }
  