.scrollup {
    position: fixed;
    right: 2.5rem;
    bottom: 10%;
    background-color: var(--title-color);
    opacity: 0.8rem;
    padding: 0.3rem 0.5rem;
    border-radius: 5rem;
    z-index: var(--z-tooltip);
    transition: 0.4s;
}

.scrollup__icon {
    font-size: 1.5rem;
    color: var(--container-color);
}

.scrollup:hover {
    background-color: var(--title-color-dark);
}

.show-scroll {
    bottom: 3rem;
}

/* ********************** Breakpoints ****************** */
/* For Large Device */
@media screen and (max-width: 992px){
    .scrollup {
        right: 1.5rem;
        padding: 0.25rem 0.4rem;
    }

    .scrollup__icon {
        font-size: 1.25rem;
    }

  }
  
  /* For Medium Device */
  @media screen and (max-width: 768px){
    .show-scroll {
        bottom: 4rem;
    }
  }
  
  /* For Small Device */
  @media screen and (max-width: 350px){
    .scrollup {
        right: 1rem;
    }
  }